import React from "react";
import styles from "./OrderDetails.module.css";
import { Table, Container, Row, Col } from "react-bootstrap";
import { Redirect } from "react-router-dom";

const OrderDetails = (props) => {

  console.log(props.orderDetails);

  let order = props.orderDetails;
  const orderAmount = () => {
    let counter = 0;
    for (let i = 0; i < order.length; i++) {
      let totalPrice = (order[i].productPrice * order[i].count);
      counter += totalPrice;
    }
    return counter
  }

  let productsElements = !order ? '...'
    : order.map((p) => (
      <tbody key={p.productId}>
        <tr>
          <td className={styles.photoContainer}><img className={styles.productPhoto} src={p.pictureUrl} alt=" " /></td>
          <td>{p.productDescription}</td>
          <td>{p.productMaterial}</td>
          <td>{p.productSizes}</td>
          <td>{p.productPrice}</td>
          <td>{p.count}</td>
          <td>{p.count * p.productPrice}</td>
        </tr>
      </tbody>
    ));

    if (!order) return <Redirect to="/profile" />;

  return (
    <>
      <h2> </h2>
      <Container fluid >
        <Row>
          <Col style={{ overflowY: "auto" }}>
            <Table className={styles.tableContainer} striped bordered hover>
              <thead>
                <tr>
                  <th></th>
                  <th>&nbsp;</th>
                  <th></th>
                  <th></th>
                  <th>&nbsp;()</th>
                  <th>&nbsp;()</th>
                  <th>C&nbsp;()</th>
                </tr>
              </thead>
              {productsElements}
              <tfoot>
                <tr>
                  <td colSpan="2">
                     :&nbsp;{orderAmount()}&nbsp;
                  </td>
                </tr>
              </tfoot>
            </Table>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default OrderDetails;