import React from "react";
import styles from "./Orders.module.css";
import { Table } from "react-bootstrap";
import { NavLink } from "react-router-dom";

const Orders = ({ orders, setOrderDetails }) => {
 const onClickLink = (products) => {
   console.log("test1")
    setOrderDetails(products)
  }
  console.log("orders-->", orders);
  let OrdersElements = !orders ? '...'
    : orders.map((o) => (
      <tbody key={o.orderId}>
        <tr>
          <td>{o.orderId}</td>
          <td>{o.buyerName}</td>
          <td>{o.buyerEmail}</td>
          <td>{o.buyerPhone}</td>
          <td>{o.buyerAddress}</td>
          <td>{o.orderAmount}</td>
          <td>{o.createTime}</td>
          <td>{o.orderStatus === 0 ? "" : ""}</td>
          <td><NavLink to="/order" onClick={() => onClickLink(o.products)}></NavLink></td>
        </tr>
      </tbody>
    ));

  return (
    <>
      <Table className={styles.tableContainer} striped bordered hover>
        <thead>
          <tr>
            <th>#</th>
            <th>&nbsp;</th>
            <th>. </th>
            <th></th>
            <th></th>
            <th>&nbsp;()</th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        {OrdersElements}
      </Table>
    </>
  );
}

export default Orders;